export const Svg = ({ className, svg }) => {
    switch (svg) {
        case 'Doctor':
            return <DoctorSvg className={className} />;

        case 'Sherrif':
            return <SherrifSvg className={className} />;

        case 'Detective':
            return <DetectiveSvg className={className} />;

        case 'Vigilante':
            return <VigilanteSvg className={className} />;

        case 'Townie':
            return <TownieSvg className={className} />;

        case 'Brawler':
            return <BrawlerSvg className={className} />;

        case 'Mermaid':
            return <MermaidSvg className={className} />;

        case 'Mafia':
            return <MafiaSvg className={className} />;

        case 'Godfather':
            return <GodfatherSvg className={className} />;

        case 'Goon':
            return <GoonSvg className={className} />;

        case 'Escort':
            return <EscortSvg className={className} />;

        default:
            throw new Error('Unexpected svg input');
    }
};
